.main {
    text-align: center;
    margin-top: 12%;
    width: 100%;
}

.divWelcome {
    font-size: 30px;
    font-family: Ink Free;
}

.form {
    margin-top: 70px;
}


.divEmail, .divPass, .divError {
    padding: 10px 40%;
    display: block;
}

.divError {
    color: brown;
}

.divSum {
    margin-top: 30px;
}

.sum {
    border: 1px solid #9C9C9C;
    outline: none;
    text-align: center;
    color: #E8E8E8;
    padding: 5px;
    background-color: #242424;
    width: 10%;
}

.sum:hover {
    background-color: #363636;
}


.navlink {
    text-decoration: none;
    color: #E8E8E8;
    position: fixed;
    bottom: 50px;
    left: 100px;
    font-size: 18px;
}

.navlink:hover {
    color: #9C9C9C;
}

.divImgLogo {
    padding: 0;
    position: fixed;
    bottom: 30px;
    right: 60px;
    transform: scale(-1, 1)
}

@media screen and (max-width: 800px), screen and (max-height: 600px)  {
    .main {
        margin-top: 16%;
    }
    .divWelcome {
        font-size: 27px;
    }
    .form {
        margin-top: 50px;
    }
    .divEmail, .divPass, .divError {
        padding: 10px 25%;
    }
    .sum {
        width: 30%;
    }

    .divImgLogo {
        display: none;
    }
    .navlink {
        position: relative;
        display: block;
        bottom: auto;
        margin-top: 100px;
        left: 0;

    }
}