.imaginaryHeader {
    height: 45px;
}

.fixed {
    position: fixed;
    width: 100%;
    background-color: #1C1C1C;
    z-index: 10;
}

.main {
    width: 100%;
    height: 45px;
    border-bottom: 1px solid #9C9C9C;
    text-align: center;
    cursor: pointer;
    display: grid;
    grid-template-columns: 2fr 3fr 3fr /*3fr 3fr*/ 3fr 3fr 3fr 2fr;
}

.pandaAvatarImg {
    width: 35px;
}

.element{
    border-left: 1px solid #9C9C9C;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    width: 100%;
    height: 100%;
    color: #E8E8E8;
}
.element:first-child{
    border: 0;
    cursor: default;
}

.icon {
    margin-left: 2%;
}

.avatar {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 1px solid #9C9C9C;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.button {
    margin-left: 7%;
}
.settings {
    position: absolute;
    top: 45px;
    right: 0;
    width: 10.4%;
    border: 1px solid #9C9C9C;
    z-index: 3;
    background-color: #1C1C1C;
}
.settings > ul {
    padding: 0;
    margin: 0;
}
.settings > ul > li {
    list-style-type: none;
    position: relative;
    text-align: center;
    padding: 10px 0;
}
.navlink {
    text-decoration: none;
    color: #E8E8E8;
}

@media screen and (max-width: 1200px) {
    .main {
        grid-template-columns: 3fr 3fr 3fr 3fr 3fr;
    }
    .settings {
        width: 39.9%;
    }
}