.main {
    height: 100%;
}

.mesWindow {
    width: 100%;
    height: 90%;
    overflow-y: auto;
    overflow-x: hidden;
    color: #E8E8E8;
    font-size: 17px;
    background-color: #171616;
}
.mes {
    width: 100%;
    position: relative;
    display: inline-block;
    margin: 20px 0;
    padding: 10px 0;
}
.mes:hover {
    background-color: #242424;
}

.mesOutput, .mesInput {

}
.mesOutput > textarea, .mesInput > textarea {
    outline: none;
    resize: none;
    background-color: #E8E8E8;
    border-radius: 10px;
    border: 1px solid #E8E8E8;
    color: #696969;
    font-size: 17px;
}
.mesOutput {
    text-align: right;
    margin-right: 3%;
}
.mesInput {
    text-align: left;
    margin-left: 3%;
}
.buttonDelete {
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.under {
    position: absolute;
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 11fr 1fr;
    grid-template-areas: "mi mb";
    min-height: 10%;
    max-height: 20%;
    bottom: 0;
    width: 100%;
    background-color: #171616;
    border-top: #9C9C9C 1px solid;
}
.input {
    background-color: #171616;
    border: none;
    border-right: #9C9C9C 1px solid;
    padding: 20px;
    color: #E8E8E8;
    word-wrap: break-word;
    resize: none;
    grid-area: mi;
    outline: none;
    font-size: 18px;
    text-overflow: ellipsis;
}
.midButton {
    grid-area: mb;
    margin: 20px;
}

/*OneMessage*/

.oneMes {
    display: inline-block;
    min-width: 20%;
    max-width: 60%;
    background-color: #E8E8E8;
    border: #E8E8E8 1px solid;
    color: #171616;
    padding: 5px 0;
    border-radius: 10px;
}
