.mesWin {
    grid-area: mw;
    display: grid;
    grid-template-areas:
            "name time"
            "text text";
    grid-template-columns: 10fr 2fr;
}
.name {
    grid-area: name;
    text-align: left;
    padding-left: 5px;
    padding-bottom: 5px;
    border-bottom: 1px solid #171616;
    word-wrap: break-word;
}
.time {
    grid-area: time;
    text-align: right;
    padding-right: 5px;
}
.text {
    grid-area: text;
    text-align: left;
    padding: 2%;
    word-wrap: break-word;
}