.right {
    text-align: center;
}

.navlink {
    outline: none;
    text-decoration: none;
    color: #E8E8E8;
}
.avatar {
    display: inline-block;
    margin-top: 80px;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    border: 1px solid #9C9C9C;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.name {
    padding: 20px 0;
    font-size: 18px;
}
.delete {
    margin-top: 550px;
    font-size: 25px;
    cursor: pointer;
}