.main {
    display: grid;
    grid-template-columns: 3fr 7fr 2fr;
    grid-template-rows: auto;
    grid-template-areas:
            "a n n"
            "a s s"
            "a l l"
            "a x x";
    padding: 40px;
}

.avatar {
    grid-area: a;
}

.avatarImg {

    height: 220px;
    width: 220px;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border: 1px solid #9C9C9C;
}

.name {
    grid-area: n;
    font-size: 17px;
    padding-bottom: 5px;
    text-align: left;
    border-bottom: 1px solid #9C9C9C;
}

.status {
    grid-area: s;
    margin-top: 50px;
    background-color: #242424;
    border-radius: 10px;
    padding: 5px 10px;
    cursor: default;
}

.location {
    grid-area: l;
    margin-top: 80px;
    cursor: default;
}

.navlink {
    text-decoration: none;
    color: #E8E8E8;
}