.field {
    padding: 10px 0;
}

/******************************************************/

.Input.input, .Input.input.error {
    border: 1px solid #9C9C9C;
    color: #E8E8E8;
    padding: 4px;
    width: 100%;
    height: 20px;
    text-align: center;
    background-color: #171616;
    outline: none;
}

.Input.input.error {
    border: 1px solid brown;
    outline: none;
}

.Input.span {
    text-align: center;
    display: block;
    background: none;
    padding-top: 15px;
    border: none;
    color: brown;
}

/******************************************************/

.InputRegister.input, .InputRegister.input.error {
    border: #9C9C9C solid 1px;
    color: #E8E8E8;
    padding: 4px;
    width: 100%;
    height: 30px;
    text-align: center;
    border-radius: 10px;
    background-color: #171616;
    outline: none;
}

.InputRegister.input.error {
    border: 1px solid brown;
    outline: none;
}

/******************************************************/

.InputWithSelect {
    width: 100%;
    position: relative;
    height: 30px;
    margin: 20px 0;
    display: flex;
    align-items: center;
    border: 1px solid #E8E8E8;
    background-color: #171616;
    outline: none;
}

.InputWithSelectInput {
    width: 87%;
    text-align: center;
    outline: none;
    padding: 0;
    max-height: 100%;
    border: 0;
    background: none;
    color: #E8E8E8;

}
.InputWithSelectIcon {
    min-width: 7%;
    max-width: 7%;
    height: 80%;
    margin-bottom: 8px;
    padding: 0 3%;
    cursor: pointer;
}

.InputWithSelectDiv {
    width: 100%;
    top: 31px;
    margin: 0;
    padding: 0;
    height: 300px;
    position: absolute;
    overflow-y: scroll;
    overflow-x: hidden;
    background: #1C1C1C;
    border: 1px solid #E8E8E8;
    border-top: none;
    z-index: 2;
}

.InputWithSelectOption {
    display: grid;
    grid-template-areas: 'ava name';
    padding: 10px 10px;
    cursor: pointer;
}
.InputWithSelectOption:hover {
    background: #363636;
}
.InputWithSelectAvatar {
    grid-area: ava;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 1px solid #E8E8E8;
    background-color: #E8E8E8;
    background-repeat: no-repeat;
    background-position: center;
    object-fit: cover;
    background-size: cover;
}
.InputWithSelectName {
    grid-area: name;
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
}

/***********************/


/************SelectRelationships***********/

.selectInfo {
    background-color: #171616;
    padding: 8px;
    color: #E8E8E8;
    outline: none;
    width: 50%;
    text-align: center;
}

.selectInfo > option {
    padding: 10px;
    text-decoration: underline;

}

.selectInfo > option:hover {
    padding: 10px;
    background-color: #9C9C9C;
}

/************Textarea***********/

.divTextarea {

}

.textarea {
    padding: 10px;
    resize: none;
    min-height: 80px;
    max-height: 250px;
    width: 70%;
    outline: none;
    background-color: #171616;
    border: 1px solid #E8E8E8;
    color: #E8E8E8;
    font-size: 17px;
}
