.main {
    display: grid;
    grid-template-columns: 2fr 8fr 2fr;
    grid-template-rows: auto;
    grid-template-areas:
            "a n n"
            "a s bf";
    border-bottom: 1px solid #E8E8E8;
    padding: 10px;
}

.avatar {
    grid-area: a;
}

.avatarImg {
    width: 200px;
    border-radius: 50%;
    border: 1px solid black
}

.name {
    grid-area: n;
}

.status {
    grid-area: s;
}

.butFollowed {
    grid-area: bf;
}