.main {
    display: grid;
    grid-template-columns: 3fr 1fr 15fr;
    grid-template-areas: "t s i";
    border-bottom: 1px solid #E8E8E8;
    padding: 10px;
}

.text {
    grid-area: t;
    font-size: 18px;
    padding-left: 30px;
}

.divImgSearch {
    grid-area: s;
    display: flex;
    justify-content: right;
    align-items: center;
}
.imgSearch {
    width: 35%;
}

.divInput {
    grid-area: i;
    display: flex;
    justify-content: left;
    align-items: center;
}
.input {
    border: 1px solid #E8E8E8;
    color: #E8E8E8;
    padding: 10px 5px;
    width: 80%;
    text-align: center;
    background-color: #171616;
    outline: none;
}