.main {
    position: relative;
}

.cross {
    position: absolute;
    right: 3%;
    top: 20px;
    cursor: pointer;
}

.mainPost {
    display: grid;
    grid-template-columns: 2fr 10fr;
    grid-template-areas:
            "z t t"
            "ava t t"
            "n t t"
            "y t t"
            "x x b";
    margin: 0 40px;
    padding: 50px;
    border-bottom: 1px ridge #E8E8E8;
}

.avatar {
    grid-area: ava;
    display: flex;
    justify-content: center;
    align-items: center;
}

.avatarImg {
    display: inline-block;
    width: 180px;
    height: 180px;
    border-radius: 20%;
    background-repeat: no-repeat;
    background-position: center;
    object-fit: cover;
    background-size: cover;
}

.name {
    grid-area: n;
    text-align: center;
    font-size: 18px;
    padding-top: 15px;
}

.text {
    grid-area: t;
    margin-left: 70px;
    padding: 0 15px;
    border: 1px solid #E8E8E8;
    display: flex;
    justify-content: center;
    align-items: center;
}
.ptext {
    max-width: 100%;
    word-wrap: break-word;
}

.buttons {
    grid-area: b;
    font-size: 16px;
}

@media screen and (max-width: 1200px) {
    .mainPost {
        grid-template-areas:
                "ava n n"
                "ava t t"
                "x t t"
                "x y b";
        margin: 0 40px;
        padding: 40px 0;
    }
    .name {
        font-size: 17px;
        margin-left: 7%;
        padding-bottom: 5px;
        text-align: left;
        border-bottom: 1px solid #9C9C9C;
    }
    .text {
        margin-left: 7%;
        margin-top: 10px;
        border: none;
        background-color: #171616;
    }
    .avatarImg {
        display: inline-block;
        width: 120px;
        height: 120px;
        border-radius: 20%;
        background-repeat: no-repeat;
        background-position: center;
        object-fit: cover;
        background-size: cover;
    }
    .buttons {
        border-top: 1px solid #E8E8E8;
    }
}