.main {
    width: 100%;
    margin-top: 120px;
    font-family: Ink Free;
    font-size: 20px;
    text-align: center;
}

.text {
    padding: 20px 0;
    font-size: 30px;
    width: 100%;
}

.divImg {
    width: 100%;
    margin-top: 70px;
}

.img {
    width: 15%;
    padding: 20px 0;
}

.divLog {
    position: absolute;
    bottom: 15%;
    left: 10%;
}

.divReg {
    position: absolute;
    bottom: 15%;
    right: 10%;
}

.navlink {
    text-decoration: none;
    color: #E8E8E8;
}

.navlink:hover {
    color: #9C9C9C;
}

@media screen and (max-width: 800px) {
    .main {
        margin-top: 10%;
        font-size: 18px;
    }
    .text {
        font-size: 27px;
    }
    .img {
        width: 25%;
    }
    .divLog {
        bottom: 8%;
    }
    .divReg {
        bottom: 8%;
    }
}