.main {
    padding: 0 20px;
    text-align: center;
}

.text {
    border-bottom: 1px solid #E8E8E8;
    padding: 10px 0;
    width: 100%;
}

.friends {
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    margin: 20px 0;
    padding: 10px 0;
}

/*ProfileFriend*/

.avatarImg {
    display: inline-block;
    width: 80px;
    height: 80px;
    border: 1px solid #E8E8E8;
    border-radius: 20%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.text, .one, .lastText {
    display: block;
    text-decoration: none;
    color: #E8E8E8;
}

@media screen and (max-width: 1200px) {
    .text, .lastText {
        display: inline-block;
        border: 1px solid #E8E8E8;
        border-right: none;
        padding: 30px 0;
        margin: 30px 0;
        width: 30%;
    }
    .lastText{
        border-right: 1px solid #E8E8E8;
    }
    .text:hover, .lastText:hover {
        background-color: #242424;
    }
}