.mainProfile {
    text-align: center;
    font-size: 16px;
    display: grid;
    grid-template-columns: 4fr 3fr 4fr;
    grid-template-areas:
            "s ava in"
            "x um ur"
            "ph ph ph";
}

.status {
    margin-top: 80px;
    grid-area: s;
    display: flex;
    justify-content: center;
    align-items: center;
}

.divStatus {
    display: grid;
    margin: 0 15%;
    width: 100%;
    grid-template-areas:
            "p"
            "t";
}

.pStatus {
    grid-area: p;
    margin: 10px 0;
}

.statusSpan {
    grid-area: t;
    background-color: #171616;
    max-height: 100px;
    overflow-y: hidden;
    padding: 10px;
    margin: 0;
    min-width: 10%;
    cursor: pointer;
    word-break: break-word;
}

.statusInput {
    outline: none;
    min-height: 40px;
    max-height: 100px;
    resize: none;
    font-size: 16px;
    border: 1px solid #E8E8E8;
    color: #E8E8E8;
    padding: 10px;
    grid-area: t;
    text-align: center;
    background-color: #1C1C1C;
}

.avatar {
    margin-top: 80px;
    grid-area: ava;
    text-align: center;
    display: flex;
    cursor: pointer;
    justify-content: center;
}

.avatarImg {
    width: 300px;
    height: 280px;
    border-radius: 10%;
    border: 1px solid #9C9C9C;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.mainName {
    grid-area: n;
    padding: 30px 0;
    font-size: 25px;
}

.info {
    margin-top: 80px;
    grid-area: in;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ul {
    width: 60%;
    margin: 0 20%;
    text-align: left;
    list-style-type: none;
}

.ul > li {
    padding: 10px;
}

.underMiddle {
    grid-area: um;
}

.underRight {
    grid-area: ur;
    display: grid;
    grid-template-columns: 30% 25% 15% 30%;
    grid-template-areas:
            "x mes mes y"
            "x fr opt y";
}
.butMes, .butOptions, .butInfo {
    grid-area: mes;
    display: inline-block;
    background-color: #242424;
    padding: 14px 0;
    font-size: 14px;
    border: black;
    border-radius: 10px;
    color: #E8E8E8;
    outline: none;
    height: 17px;
    cursor: pointer;
    text-decoration: none;
}
.butFriend {
    grid-area: fr;
}
.butOptions {
    grid-area: opt;
    margin-left: 10%;
    width: 90%;
}
.butMes:hover, .butOptions:hover, .butInfo:hover  {
    background-color: #363636;
    color: #9C9C9C;
}

.photos {
    grid-area: ph;
    margin-top: 60px;
    overflow-x: auto;
    white-space: nowrap;
    display: inline;
    padding-bottom: 20px;
}

.photosImg {
    cursor: pointer;
    display: inline-block;
    padding-bottom: 0;
    margin-left: 10px;
    width: 10%;
    height: 200px;
    border-radius: 5%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}


.photos::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: #363636;
}
.photos::-webkit-scrollbar
{
    width: 12px;
    background-color: #363636;
}
.photos::-webkit-scrollbar-thumb
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #696969;
}

.activePhoto {
    position: fixed;
    width: 100%;
    height: 105%;
    background-color: rgba(31, 31, 31, .9);
    z-index: 200;
    margin-top: -45px;
}

.activePhoto > img {
    -ms-interpolation-mode: bicubic;
    max-height: 90%;
    margin: 20px 0;
}

@media screen and (max-width: 1200px) {
    .mainProfile {
        grid-template-columns: 5fr 5fr;
        grid-template-areas:
                "ava ava"
                "n n"
                "divS divS"
                "mes fr"
                "inf opt"
                "info info"
                "ph ph"
                "r r";
    }
    .avatarImg {
        width: 250px;
        height: 250px;
    }
    .mainName{
        font-size: 22px;
    }
    .divStatus {
        grid-area: divS;
        font-size: 16px;
        width: 40%;
        margin: 0 30% 40px 30%;
    }
    .butMes, .butFriend, .butInfo, .butOptions {
        width: 80%;
        margin: 0 0 40px 10%;
    }
    .butOptions {

    }
    .butInfo {
        grid-area: inf;
    }
    .info {
        margin-top: 0;
        margin-bottom: 30px;
        padding: 10px 0;
        border-top: 1px solid #E8E8E8;
        border-bottom: 1px solid #E8E8E8;
        grid-area: info;
        display: block;
    }
    .ul {
        width: 100%;
        margin: 0;
        padding: 10px 0;
        text-align: center;
    }
    .ul > li {
        padding: 5px;
    }

    .butCloseInfo {
        cursor: pointer;
        float: right;
        width: 20%;
        margin: 0;
        padding: 0px 20px 5px 0;
    }
    .butCloseInfo:hover {
       color: #9C9C9C;
    }
    .photos {
        margin-top: 0;
        margin-bottom: 10px;
    }
    .photosImg {
        width: 20%;
        height: 120px;
    }
}
