.main {
    display: grid;

    grid-template-columns: 3fr 9fr;
    grid-template-areas:
            "i i"
            "m p";
}

.info {
    grid-area: i;
    border-bottom: 1px solid #E8E8E8;
}
.modules {
    grid-area: m;
    border-right: 1px solid #E8E8E8;
}
.posts {
    grid-area: p;
}

@media screen and (max-width: 1200px) {
    .main {
        display: block;
    }
    .modules {
        border-right: none;
    }
}
