.main {
    position: absolute;
    height: 95%;
    width: 100%;
    border-bottom: #9C9C9C solid 1px;
    overflow-y: hidden;
    display: grid;
    grid-template-rows: 93% 7%;
    grid-template-areas:
            "t"
            "b";
}

.top {
    text-align: center;
    overflow-y: auto;
    overflow-x: hidden;
    position: relative;
    grid-area: t;
}

.location {
    font-size: 18px;
    padding-top: 30px;
}

.headers {
    font-size: 18px;
    padding: 20px 0;
}

.paragraphs {


}

.age {
    font-size: 18px;
    padding: 10px 0 20px 0;
}
.divInput {

}
.input {
    background-color: #171616;
    border: 1px solid #9C9C9C;
    padding: 8px 0;
    text-align: center;
    outline: none;
    color: #E8E8E8;
}


.pSort {
    margin-bottom: 20px;
}

.menu {
    position: relative;
    width: 100%;
    padding: 12px 0;
    bottom: 0;
    font-size: 35px;
    z-index: 2;
    background-color: #1C1C1C;
    border-top: #9C9C9C solid 1px;
    grid-area: b;
}

.trash {
    float: left;
    cursor: pointer;
    margin-left: 47%;
}

.icon {
    float: right;
    cursor: pointer;
    margin-right: 8%;
}

@media screen and (max-height: 900px) {
    .menu {
        font-size: 25px;
    }
}