.main {
    text-align: center;
    padding: 50px 22%;
}

.mainFriend {
    display: grid;
    grid-template-columns: 10fr 3fr;
    grid-template-areas: "f b";
    border-bottom: 1px solid #E8E8E8;
}

.friend {
    grid-area: f;
}
.button {
    grid-area: b;
    margin: 100px 20%;
}

.one {
    display: grid;
    grid-template-columns: 1fr 11fr;
    grid-template-rows: auto;
    grid-template-areas:
            "a n"
            "a s"
            "a x";
    padding: 40px;
}

.avatar {
    grid-area: a;
}

.avatarImg {
    height: 180px;
    width: 180px;
    border-radius: 10%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border: 1px solid #9C9C9C;
}

.name {
    grid-area: n;
    font-size: 17px;
    margin-left: 7%;
    padding-bottom: 5px;
    text-align: left;
    border-bottom: 1px solid #9C9C9C;
}

.status {
    grid-area: s;
    text-align: left;
    margin-left: 7%;
    margin-top: 40px;
    background-color: #242424;
    padding: 5px 10px;
}


.navlink {
    text-decoration: none;
    color: #E8E8E8;
}

@media screen and (max-width: 1000px) {
    .main {
        padding: 40px 15%;
    }
    .mainFriend {
        display: block;
    }
    .one {
        grid-template-areas:
                "a n"
                "a s"
                "a x";
        padding: 40px 0;
    }
    .avatarImg {
        height: 100px;
        width: 100px;
    }
    .status {
        margin-top: 10px;
        padding: 5px;
        font-size: 15px;
    }
    .button {
        display: none;
    }

}