.writePost {
    padding: 50px;
    text-align: center;
    border-bottom: 1px solid #E8E8E8;
}

.butPost {
    margin-top: 30px;
    outline: none;
    width: 10%;
    background-color: #242424;
    border: #242424;
    border-radius: 10px;
    color: #E8E8E8;
    padding: 10px 0;
}

@media screen and (max-width: 1000px) {
    .writePost {
        padding: 40px 0;
        border-top: 1px solid #E8E8E8;
    }
    .butPost {
        margin-top: 30px;
        width: 40%;
    }
}