.main {
    overflow: hidden;
}
.left, .middle, .right {
    overflow-y: hidden;
    position: absolute;
    bottom: 0;
    top: 45px;
}
.left {
    overflow-y: auto;
    width: 20%;
}
.middle {
    width: 60%;
    left: 20%;
    border: 1px solid #9C9C9C;
}
.noActive {
    margin-top: 35%;
    width: 100%;
    text-align: center;
    font-family: cursive;
}
.right {
    overflow-y: auto;
    width: 20%;
    right: 0;
}
.under {
    position: absolute;
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 11fr 1fr;
    grid-template-areas: "mi mb";
    min-height: 10%;
    max-height: 20%;
    bottom: 0;
    left: 0;
    width: 100%;
}
.input {
    background-color: #171616;
    border: none;
    border-top: #9C9C9C 1px solid;
    padding: 40px;
    color: #E8E8E8;
    grid-area: mi;
    outline: none;
    overflow: hidden;
    text-overflow: ellipsis;
}
.midButton {
    grid-area: mb;
}