.text {
    text-align: center;
    grid-area: t;
    font-size: 18px;
    padding: 20px 0;
    margin-bottom: 50px;
    border-bottom: 1px solid #E8E8E8;
}

.form {
    text-align: center;
}
.main {
    margin-top: 5%;
    width: 20%;
    display: inline-block;
}
.input, .gender {
    padding: 10px 0;
}
.gender > div {
    display: inline;
    padding: 0 10px;
}

.but {
    position: absolute;
    left: 42%;
    bottom: 50px;
    width: 16%;
    padding: 20px 0;
    font-size: 22px;
    text-align: center;
    background-color: #E8E8E8;
    border: #171616 1px solid;
    border-radius: 10px;
    cursor: pointer;
    outline: none;
}

.but:hover {
    background-color: #9C9C9C;
}

.navlink {
    text-decoration: none;
    color: #E8E8E8;
    position: fixed;
    bottom: 50px;
    left: 100px;
}

.navlink:hover {
    color: #9C9C9C;
}

.divImgLogo {
    padding: 0;
    position: fixed;
    bottom: 30px;
    right: 60px;
    transform: scale(-1, 1)
}

@media screen and (max-width: 1000px), screen and (max-height: 700px) {
    .text {
        font-size: 16px;
        padding: 15px 0;
    }
    .main {
       margin-top: 0;
        width: 50%;
    }
    .input, .gender {
        padding: 8px 0;
    }
    .but {
        position: relative;
        display: inline-block;
        left: 0;
        bottom: 0;
        width: 50%;
        margin-top: 30px;
        padding: 5px 0;
        font-size: 17px;
        border: 1px solid #9C9C9C;
        color: #E8E8E8;
        background-color: #242424;
    }

    .divImgLogo {
        display: none;
    }
    .navlink {
        position: relative;
        display: block;
        padding: 10px 0;
        text-align: center;
        width: 100%;
        margin-top: 50px;
        bottom: 0;
        left: 0;
    }
}