.main {
    position: absolute;
    height: 95%;
    width: 100%;
    border-bottom: #9C9C9C solid 1px;
    overflow-y: hidden;
    display: grid;
    grid-template-rows: 93% 7%;
    grid-template-areas:
            "t"
            "b";
}

.top {
    grid-area: t;
}
.elemNavbars {
    padding: 15px;
}

.elemNavbars:hover {
    background-color: #363636;
    cursor: pointer;
}

.navlink {
    text-decoration: none;
    color: #E8E8E8;
}

.menu {
    position: relative;
    width: 100%;
    padding: 1px 0;
    bottom: 0;
    font-size: 35px;
    z-index: 2;
    background-color: #1C1C1C;
    border-top: #9C9C9C solid 1px;
    grid-area: b;
}

.icon {
    float: right;
    cursor: pointer;
    margin-right: 4%;
    width: 13%;
}

.navLink {
    text-decoration: none;
    color: #E8E8E8;
}