.all {
    display: inline-block;
    width: 70%;
}

.main {
    width: 100%;
    padding: 20px 0;
    display: flex;
    align-items: center;
}

.span {
    width: 100%;
    position: relative;
    height: 30px;
    border: 1px solid #E8E8E8;
    background-color: #171616;
    display: flex;
    align-items: center;
}

.input {
    width: 87%;
    text-align: center;
    outline: none;
    padding: 0;
    max-height: 100%;
    border: 0;
    background: none;
    color: #E8E8E8;

}
.icon {
    min-width: 7%;
    max-width: 7%;
    height: 80%;
    margin-bottom: 8px;
    padding: 0 3%;
    cursor: pointer;
}

.mainDivInput {
    width: 100%;
    top: 31px;
    margin: 0;
    padding: 0;
    height: 300px;
    position: absolute;
    overflow-y: scroll;
    overflow-x: hidden;
    background: #1C1C1C;
    border: 1px solid #E8E8E8;
    border-top: none;
    z-index: 2;
}

.option {
    padding: 10px 0;
    cursor: pointer;
}
.option:hover {
    background: #363636;
}
