.search {
    padding: 10px;
    text-align: center;
}

.person {
    display: grid;
    grid-template-columns: 3fr 8fr 0.5fr;
    grid-template-areas:
            "a n d"
            "a m m";
    padding: 20px 10px;
    border-bottom: 1px solid #9C9C9C;
}
.person:hover {
    background-color: #242424;
    cursor: pointer;
}
.avatar {
    grid-area: a;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 1px solid #9C9C9C;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.name {
    grid-area: n;
    margin-left: 4%;
}
.delete {
    grid-area: d;
    z-index: 50;
}
.mes {
    grid-area: m;
    padding: 10px 1% 10px 4%;
    display: grid;
    grid-template-areas: "ava t time";
    grid-template-columns: 1fr 10fr 1fr;
}
.avatarMes {
    grid-area: ava;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 1px solid #9C9C9C;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.text {
    grid-area: t;
    height: 30px;
    padding-left: 5%;
    padding-top: 5px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.time {
    grid-area: time;
    display: flex;
    height: 30px;
    align-items: center;
}