.main {
    display: grid;
    grid-template-columns: 80% 20%;
    grid-template-rows: auto;
    grid-template-areas:
            "h n"
            "b n"
            "x n";
    border-color: #4F4F4F;
}

.header {
    grid-area: h;
}

.body {
    grid-area: b;
    border-right: #9C9C9C solid 1px;
}

.navbar {
    position: absolute;
    width: 20%;
    height: 100%;
    right: 0;
    border-left: #9C9C9C solid 1px;
}
