.person {
    margin: 5%;
    display: grid;
    grid-template-columns: 10fr 2fr;
    grid-template-areas: "op b";
    border-bottom: 1px solid #E8E8E8;
}

.onePerson {
    grid-area: op;
}

.butFriend {
    grid-area: b;
    margin: 120px 10%;
}

.navlink {
    text-decoration: none;
    color: #E8E8E8;
}